import React from 'react'
import {MapContainer, TileLayer, Marker, Popup} from 'react-leaflet';
import 'leaflet/dist/leaflet.css';
import L from 'leaflet';

const IconLocation = L.icon({
    iconUrl: 'data:image/png;base64,',
    iconAnchor:   [20, 41], // point of the icon which will correspond to marker's location
    popupAnchor:  [0, -41],
    iconSize: (25, 41),
    className: "Lealet-venue-icon",
})
export default class MapView extends React.Component{
    render(){
        return(
        <MapContainer center={{lat:'-34.590771', lng:'-58.447665'}} zoom={13}>
            <TileLayer url='https://{s}.tile.openstreetmap.org/{z}/{x}/{y}.png' attribution='&copy; <a href="http://osm.org/copyright">OpenStreetMap</a> contributors'/>
            
            <Marker position={{lat:'-34.590771', lng:'-58.447665'}} icon={IconLocation}>
                <Popup>
                    Av. Corrientes 6237, CABA 
                </Popup>
            </Marker>
        </MapContainer>
        )
    }
}